            title-pointer
        >
            <div class="rtroot">
                <div class="buttons">
                    <div
                        @click="onToggle(0)"
                        class="button"
                        :class="[curButton == 0 && 'button-hl']"
                    >
                        国内政策
                    </div>
                    <div
                        @click="onToggle(1)"
                        class="button"
                        :class="[curButton == 1 && 'button-hl']"
                    >
                        海外政策
                    </div>
                </div>
            </div>
        </UseTitle>
        <First v-if="curButton === 0"></First>
        <Second v-else></Second>
    </div>
</template>

<script setup>
import { ref } from "vue";
import First from "./First.vue";
import Second from "./Second.vue";

const actived = ref(0);

const curButton = ref(0);

function onTitleClick(value) {
    actived.value = value;
}

function onSetTitleBg(value) {
    return actived.value === value
