<template>
    <div class="second">
        <div class="select-option">
            <el-select v-model="selectValue" size="large">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                />
            </el-select>
        </div>

        <div class="tabs">
            <div
                :class="[{ selectStyle: tab.index == tabIndex }, 'tab']"
                v-for="tab in tabs"
                :key="tab.index"
                @click="handleChange(tab)"
            >
                {{ tab.label }}
            </div>
        </div>
        <div class="table">
            <VLine>
                <div class="tr th-bg tr1">政策热度排行</div>
                <div class="tr th-bg tr2">政策名称</div>
                <div class="tr th-bg tr3">行业类型</div>
                <div class="tr th-bg tr4">政策类型</div>
                <div class="tr th-bg tr5">补助企业</div>
                <div class="tr th-bg tr6">补助金额</div>
            </VLine>
            <div class="scrollBox">
                <VLine
                    class="vline"
                    v-for="(item, i) in tableCom"
                    :key="item.index"
                >
                    <div
                        :class="[
                            item.index == rankingThree[i]
                                ? 'color_FFBD46'
                                : item.index == 4 || item.index == 5
                                ? 'color_00EFFF'
                                : '',
                            'tr',
                            'tr1',
                        ]"
                    >
                        <img
                            v-if="item.index == rankingThree[i]"
                            src="@/assets/qian3.png"
                            class="img"
                        />
                        <img
                            v-else-if="item.index == 4 || item.index == 5"
                            src="@/assets/45.png"
                            class="img"
                        />
                        <span v-else class="img"></span>
                        {{ item.index }}
                    </div>
                    <div
                        :class="[
                            item.index == rankingThree[i]
                                ? 'color_FFBD46'
                                : item.index == 4 || item.index == 5
                                ? 'color_00EFFF'
                                : '',
                            'tr',
                            'tr2',
                        ]"
                    >
                        {{ item.label }}
                    </div>
                    <div class="tr tr3">{{ item.playCount }}</div>
                    <div class="tr tr4">{{ item.count }}</div>
                    <div class="tr tr5">{{ item.pay }}</div>
                    <div class="tr tr6">{{ item.works }}</div>
                </VLine>
            </div>
            <use-pagination
                :pages="tableTotal"
                :currentPage="pagination.currentPage"
                :pageSize="pagination.pageSize"
                :total="pagination.total"
                @goHome="goHome"
                @goLast="goLast"
                @handleSizeChange="handleSizeChange"
                @handleCurrentChange="handleCurrentChange"
            />
            <!-- <div class="pagination">
                <span class="current">共{{ tableTotal }}页</span>
                <span class="current frame">首页</span>
                <el-pagination
                    v-model:currentPage="pagination.currentPage"
                    background
                    :page-size="pagination.pageSize"
                    layout=" prev, pager, next"
                    :total="pagination.total"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                />
                <span class="current frame">尾页</span>
            </div> -->
        </div>
    </div>
</template>
<script setup>
import VLine from "./VLine";
import { enterpriseList,policys } from "@/utils/enterpriseList";
import usePagination from "@/components/usePagination.vue";
import { computed, ref } from "vue";
// 选择框内容
let selectValue = ref("");
const options = ref([
    { label: "全部类别", value: "" },
    { label: "文化产业", value: "1" },
    { label: "数字文化产业", value: "2" },
    { label: "动漫游戏", value: "3" },
    { label: "数字贸易", value: "4" },
    { label: "文化精品扶持", value: "5" },
    { label: "服务贸易", value: "6" },
]);

const tabs = ref([
    { index: "1", label: "全部类别", value: "" },
    { index: "2", label: "文化产业", value: "1" },
    { index: "3", label: "数字文化产业", value: "2" },
    { index: "4", label: "动漫游戏", value: "3" },
    { index: "5", label: "数字贸易", value: "4" },
    { index: "6", label: "文化精品扶持", value: "5" },
    { index: "7", label: "服务贸易", value: "6" },
]);
const tabIndex = ref("1");
const handleChange = (tab) => {
    tabIndex.value = tab.index;
};
const rankingThree = ref([1, 2, 3]);
const pagination = ref({
    currentPage: 1,
    pageSize: 10,
    total: 20,
});
// 前端分页
const tableCom = computed(() => {
    const { currentPage, pageSize } = pagination.value;
    let data = list.value.find(
        (item) => item.tabValue == tabIndex.value
    ).tableList;
    // 总数
    pagination.value.total = data.length;
    let table = data.slice(
        (currentPage - 1) * pageSize,
        currentPage * pageSize
    );
    return table;
});
// 总共页数
const tableTotal = computed(() => {
    const { currentPage, total, pageSize } = pagination.value;
    let count = Math.round(total / pageSize);
    return count;
});
// 动态添加死数据
function addList() {
    tabs.value.forEach((item) => {
        let tabValue = item.index,
            tableList = [];
        for (let i = 0; i < pagination.value.total; i++) {
            let num = Math.floor(41 * Math.random());
            tableList.push({
                index: i+1,
                label: policys[i].name,
                playCount: policys[i].type,
                count: policys[i].ptype,
                pay: `${rand(4)}家`,
                works: `${policys[i].pcount ?? rand(3)}万元`,
            });
        }
        list.value.push({
            tabValue,
            tableList,
        });
    });
}
const rand = (val) => {
    let r = Math.floor(Math.random() * Math.pow(10, val));
    return r;
};
// 数据源
const list = ref([]);
addList();

const goHome = () => {
    pagination.value.currentPage = 1;
};
const goLast = () => {
    pagination.value.currentPage = tableTotal.value;
};
const handleSizeChange = (val) => {
    pagination.value.currentPage = val;
};
const handleCurrentChange = (val) => {
    pagination.value.currentPage = val;
};
</script>

