<template>
    <Overlay v-if="modelValue" :style="{ zIndex }">
        <div class="o-c-root">
            <div class="o-content" :style="{ width, height }">
                <div class="title">{{ title }}</div>
                <div class="slot">
                    <slot></slot>
                </div>
                <div class="close" @click="onClose">
                    {{ closeText }}
                </div>
            </div>
        </div>
    </Overlay>
</template>

<script setup>
import Overlay from "@/components/Overlay";

import { toRefs } from "vue";
const emits = defineEmits(["update:modelValue", "close"]);
const props = defineProps({
    modelValue: {
        type: Boolean,
        default: false,
    },
    zIndex: {
        type: Number,
        default: 10,
    },
    width: {
        type: String,
        default: "90%",
    },
    height: {
        type: String,
        default: "90%",
    },

    closeText: {
        type: String,
        default: "关闭",
    },
    title: {
        type: String,
        default: "",
    },
});

const { width, height, modelValue } = toRefs(props);

function onClose() {
    emits("close");
    emits("update:modelValue", false);
}
</script>

