    background-image: url("@/assets/dbg.png");
    background-size: 100% 100%;
    position: relative;
    box-sizing: border-box;
    padding: 60px;

    .title,
    .close {
        position: absolute;
        font-size: 24px;
        left: 50%;
        transform: translate(-50%, 0);
        color: #fff;
    }
    .title {
        top: 0px;
        width: 350px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .close {
        bottom: 0px;
        width: 230px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    .slot {
        width: 100%;
        height: 100%;
    }
}

