function initEcharts() {
    const element = document.querySelector(`.topmiddle`);
    const myChart = echarts.init(element);
    myChart.setOption(getOptions());
    myChart.on("click", (params) => {
        dialogShow.value = true;
    });
}

function getOptions() {
    const seriesData = [
        { value: 1600, name: "欧洲" },
        { value: 1500, name: "美洲" },
        { value: 1400, name: "亚洲" },
        { value: 1300, name: "非洲" },
        { value: 1200, name: "北美" },
        { value: 1100, name: "东南亚" },
        { value: 800, name: "其他" },
    ];

    return {
        tooltip: {
            trigger: "item",
        },
        legend: {
            top: "center",
            right: "10%",
            width: "100px",
            itemStyle: {
                borderWidth: 0,
            },
            textStyle: {
                color: "#fff",
                rich: {
                    percent: {
                        color: "#555",
                        padding: [0, 2],
                    },
                },
            },
            formatter(name) {
                const total = seriesData
                    .map((item) => item.value)
                    .reduce((pre, next) => pre + next);
                const current = seriesData.find((item) => item.name === name);
                const percent = (current.value / total).toFixed(1) * 100 + "%";
                return `${name} {percent|${percent}} ${current.value}`;
            },
        },

        series: [
            {
                name: "媒体类别分析",
                type: "pie",
                radius: ["15%", "70%"],
                center: ["30%", "50%"],
                avoidLabelOverlap: false,
                type: "pie",
                roseType: "area",
                itemStyle: {
                    borderRadius: 0,
                    borderWidth: 2,
                    borderRadius: 3,
                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {
                    label: {
                        show: true,
                        fontSize: fontSize(0.16),
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: seriesData,
            },
        ],
    };
}

