<script setup>
import { reactive, computed, watch, ref } from "vue";
import useImmediateTime from "@/hooks/useImmediateTime";

import Square from "./Square.vue";

function getMathNumberBetween1k_9k() {
    return ~~(Math.random() * 8000 + 1000);
}

function getMathNumberBetween1_9() {
    return ~~(Math.random() * 8 + 1);
}

const state = reactive({
    a: getMathNumberBetween1k_9k(),
    b: getMathNumberBetween1k_9k(),
    c: getMathNumberBetween1k_9k(),
    d: getMathNumberBetween1k_9k(),
    // e: getMathNumberBetween1k_9k(),
});

const previousTotal = ref(0);
const previousA = ref(0);
const previousB = ref(0);
const previousC = ref(0);
const previousD = ref(0);
// const previousE = ref(0);

const changer = reactive({
    a: false,
    b: false,
    c: false,
    d: false,
    // e: false,
});

const total = computed(() =>
    Object.values(state).reduce((per, total) => per + total, 0)
);

watch(total, (newvalue, oldvalue) => {
    previousTotal.value = oldvalue;
});

watch(
    () => state.a,
    (newvalue, oldvalue) => {
        previousA.value = oldvalue;
        changer.a = true;
        virtualCountAddAnimation("a");
    }
);
watch(
    () => state.b,
    (newvalue, oldvalue) => {
        previousB.value = oldvalue;
        changer.b = true;
        virtualCountAddAnimation("b");
    }
);
watch(
    () => state.c,
    (newvalue, oldvalue) => {
        previousC.value = oldvalue;
        changer.c = true;
        virtualCountAddAnimation("c");
    }
);
watch(
    () => state.d,
    (newvalue, oldvalue) => {
        previousD.value = oldvalue;
        changer.d = true;
        virtualCountAddAnimation("d");
    }
);
// watch(
//     () => state.e,
//     (newvalue, oldvalue) => {
//         previousE.value = oldvalue;
//         changer.e = true;
//         virtualCountAddAnimation("e");
//     }
// );

const { current, date } = useImmediateTime();

// 模拟数字增加数量闪动效果
function virtualCountAddAnimation(k) {
    setTimeout(() => {
        changer[k] = false;
    }, 2 * 1e3);
}

// 模拟定时器
countAnimate();

function countAnimate() {
    setTimeout(() => {
        Object.keys(state).forEach((k) => {
            state[k] += getMathNumberBetween1_9();
        });
        countAnimate();
    }, 5 * 1000);
}

// total 数字转字符串

const totalStrs = (value) => `${value}`.split("");
</script>

<template>
    <div class="layout">
        <div class="title">
            <div class="time">
                <img class="time-icon" src="@/assets/time.png" alt="" />
                <div>
                    <div class="hhmmss">{{ current }}</div>
                    <div class="yyyymmdd">{{ date }}</div>
                </div>
            </div>
            <div class="text">文化出海驾驶舱</div>
            <div class="tmp">
                <img class="time-icon" src="@/assets/weather.png" alt="" />
                <div>
                    <div class="tmp-number">20°~28°</div>
                    <div class="tmp-string">晴转多云</div>
                </div>
            </div>
        </div>

        <div class="subtitle">
            <img
                :style="{
                    width: '2em',
                }"
                src="@/assets/zuo.png"
                alt=""
            />
            <div>出口</div>
            <div class="subtitle-count">
                <CountTo
                    :start="previousTotal"
                    :end="total"
                    :autoPlay="true"
                    :duration="4000"
                    separator=""
                >
                    <template #default="{ value }">
                        <div
                            class="colorfulBlock"
                            v-for="(item, index) in totalStrs(value)"
                            :item="index"
                        >
                            {{ item }}
                        </div>
                    </template>
                </CountTo>
            </div>
            <div>总额</div>
            <img
                :style="{
                    width: '2em',
                }"
                src="@/assets/you.png"
                alt=""
            />
        </div>

        <div class="menu">
            <div class="m-item">
                <Square title="出海作品数">
                    <CountTo
                        :start="previousA"
                        :end="state.a"
                        :autoPlay="true"
                        separator=""
                        :duration="4000"
                    >
                        <template #default="{ value }">
                            <span class="m-count">
                                <span>{{ value }}
                                &nbsp;<small style="font-size:12px;">部</small>
                                </span>
                                <Transition
                                    enter-active-class="animate__animated animate__bounceIn"
                                    leave-active-class="animate__animated animate__bounceOut"
                                >
                                    <div
                                        class="countAddValue"
                                        v-show="changer.a"
                                    >
                                        +{{ state.a - previousA }}
                                    </div>
                                </Transition>
                            </span>
                        </template>
                    </CountTo>
                </Square>
            </div>
            <div class="m-item">
                <Square title="本月出口额">
                    <CountTo
                        :start="previousB"
                        :end="state.b"
                        separator=""
                        :autoPlay="true"
                        :duration="4000"
                    >
                        <template #default="{ value }">
                            <span class="m-count">
                                <span>{{ value }}
                                &nbsp;<small style="font-size:12px;">万元</small>

                                </span>
                                <Transition
                                    enter-active-class="animate__animated animate__bounceIn"
                                    leave-active-class="animate__animated animate__bounceOut"
                                >
                                    <div
                                        class="countAddValue"
                                        v-show="changer.b"
                                    >
                                        +{{ state.b - previousB }}
                                    </div>
                                </Transition>
                            </span>
                        </template>
                    </CountTo>
                </Square>
            </div>
            <div class="m-item">
                <Square title="本月补贴兑现额">
                    <CountTo
                        :start="previousC"
                        :end="state.c"
                        :autoPlay="true"
                        separator=""
                        :duration="4000"
                    >
                        <template #default="{ value }">
                            <span class="m-count">
                                <span>{{ value }}
                                      &nbsp;<small style="font-size:12px;">万元</small>
                                </span>
                                <Transition
                                    enter-active-class="animate__animated animate__bounceIn"
                                    leave-active-class="animate__animated animate__bounceOut"
                                >
                                    <div
                                        class="countAddValue"
                                        v-show="changer.c"
                                    >
                                        +{{ state.c - previousC }}
                                    </div>
                                </Transition>
                            </span>
                        </template>
                    </CountTo>
                </Square>
            </div>
            <div class="m-item">
                <Square title="平台用户数">
                    <CountTo
                        :start="previousD"
                        :end="state.d"
                        separator=""
                        :autoPlay="true"
                        :duration="4000"
                    >
                        <template #default="{ value }">
                            <span class="m-count">
                                <span>{{ value }}
                                      &nbsp;<small style="font-size:12px;">人</small>
                                </span>
                                <Transition
                                    enter-active-class="animate__animated animate__bounceIn"
                                    leave-active-class="animate__animated animate__bounceOut"
                                >
                                    <div
                                        class="countAddValue"
                                        v-show="changer.d"
                                    >
                                        +{{ state.d - previousD }}
                                    </div>
                                </Transition>
                            </span>
                        </template>
                    </CountTo>
                </Square>
            </div>
        </div>

        <BaiduMap v-bind="$attrs"></BaiduMap>
        <div class="left">
            <slot name="left"></slot>
        </div>
        <div class="center">
            <slot></slot>
        </div>
        <div class="right">
            <slot name="right"></slot>
        </div>
    </div>
</template>

