<template>
    <div class="home">
        <Layout @pointerClick="onPointClick">
            <template #left>
                <TopLeft />
                <TopMiddle />
                <TopBottom />
            </template>
            <BottomLeft></BottomLeft>
            <template #right>
                <!-- <Analysis></Analysis> -->
                <RightTop />
                <RightMiddle />
                <RightBottom />
            </template>
        </Layout>
        <PointModal :city-name="dialog.info.city" v-model="dialog.show">
        </PointModal>
    </div>
</template>

<script setup>
import Layout from "@/components/Layout/index.vue";

import PointModal from "./Modals/PointModal";

/**Blocks*/
import TopLeft from "./BlocksOfHome/LeftTop";
import TopMiddle from "./BlocksOfHome/LeftMiddle";
import TopBottom from "./BlocksOfHome/LeftBottom";

import RightTop from "./BlocksOfHome/RightTop";
import RightMiddle from "./BlocksOfHome/RightMiddle";
import RightBottom from "./BlocksOfHome/RightBottom";

import BottomLeft from "./BlocksOfHome/BottomLeft";

import { reactive, provide, computed } from "vue";

const dialog = reactive({
    show: false,
    info: {},
});

provide(
    "POINT_INFO",
    computed(() => dialog.info.info)
);

/** point 地图点*/
function onPointClick(point) {
    dialog.show = true;
    console.log("point:", point);
    dialog.info = { ...point };
}
</script>

<style lang="scss" scoped>
.home {
    height: 100vh;
