<template>
    <div class="pagination">
        <span v-if="pagesState" class="current">共{{ pages }}页</span>
        <span v-if="currentHomeBtn" class="current cursor frame" @click="goHome"
            >首页</span
        >
        <el-pagination
            v-model:currentPage="currentPage"
            background
            :page-size="pageSize"
            layout=" prev, pager, next"
            :total="total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
        />
        <span v-if="currentLastBtn" class="current cursor frame" @click="goLast"
            >尾页</span
        >
    </div>
</template>
<script setup>
defineProps({
    pages: {
        type: Number,
        default: 0,
    },
    //  总页数 是否展示
    pagesState: {
        type: Boolean,
        default: true,
    },
    //  首页按钮 是否展示
    currentHomeBtn: {
        type: Boolean,
        default: true,
    },
    //  尾页按钮 是否展示
    currentLastBtn: {
        type: Boolean,
        default: true,
    },
    currentPage: {
        type: [String, Number],
        default: null,
    },
    pageSize: {
        type: [String, Number],
        default: null,
    },
    total: {
        type: Number,
        default: null,
    },
});
// 定义父组件的自定义事件，子组件以触发父组件的自定义事件
const emits = defineEmits([
    "goHome",
    "goLast",
    "handleSizeChange",
    "handleCurrentChange",
]);
const goHome = () => {
    emits("goHome");
};
const goLast = () => {
    emits("goLast");
};
const handleSizeChange = (val) => {
    emits("handleSizeChange", val);
};
const handleCurrentChange = (val) => {
    emits("handleCurrentChange", val);
};
</script>
<style lang="scss" scoped>
.pagination {
    margin-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: #fff;
    .current {
        font-size: 16px;
        margin-right: 16px;
    }

    .frame {
        border: 1px solid #707070;
        padding: 3px 6px;
        border-radius: 4px;
    }
    ::v-deep .el-pagination.is-background .btn-next,
    ::v-deep .el-pagination.is-background .btn-prev,
    ::v-deep .el-pagination.is-background .el-pager li {
        background: transparent;
        border: 1px solid #707070;
    }
}
.cursor {
    cursor: pointer;
}
</style>
