<template>
    <div class="useTitle">
        <div class="titleBlock" @click="onTitleClick">
            <div
                class="titleText"
                :style="{
                    cursor: titlePointer ? 'pointer' : 'auto',
                }"
            >
                {{ title }}
                <div class="rightSlot">
                    <slot name="right"></slot>
                </div>
            </div>
            <div class="background"></div>
        </div>
        <slot></slot>
    </div>
</template>

<script setup>
import { toRefs, computed } from "vue";
const props = defineProps({
    title: {
        type: String,
        default: "标题",
    },
    titleBg: {
        type: String,
        default: require("@/assets/usetitle.png"),
    },

    titlePointer: {
        type: Boolean,
        default: false,
    },
});

const emits = defineEmits(["titleClick"]);

const { titleBg } = toRefs(props);

const titleBgComputed = computed(() => `url("${titleBg.value}")`);

function onTitleClick() {
    emits("titleClick");
}
</script>

<style lang="scss" scoped>
.useTitle {
    box-sizing: border-box;
    .titleBlock {
        height: 50px;
        padding: 0 20px;
        padding-bottom: 2px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;

        .rightSlot {
            position: absolute;
            right: 40px;
            top: 30%;
            transform: translate(0%,-30%);
        }

        .titleText {
            color: #fff;
            font-size: 17px;
        }

        .background {
            width: 100%;
            height: 7px;
            margin-top: 4px;
            background-size: 100% 100%;
            background-image: v-bind(titleBgComputed);
            bottom: 0;
        }
    }
}
</style>
