<template>
    <div class="flow" :class="className">
        <div
            class="content"
            :style="{
                'background-image': `url('${contentBg}')`,
            }"
        >
            <slot></slot>
        </div>
        <img
            v-if="next"
            :style="arrowStyles"
            class="arrow"
            src="./imgs/a.png"
            alt=""
        />
    </div>
</template>

<script setup>
/**flow组件设计 */

import { computed } from "@vue/reactivity";

const props = defineProps({
    arrowDirection: {
        type: String,
        default: "left",
    },
    next: {
        type: Boolean,
        default: true,
    },
    className: {
        type: String,
        default: "",
    },
    contentBg: {
        type: String,
        default: require("./imgs/b.png"),
    },
});

const arrowStyles = computed(() => {
    const styles = {
        left: {},
        bottom: {
            left: "50%",
            bottom: "-0.08rem",
            transform: "translate(-50%,0) rotate(90deg)",
        },
        right: {
            top: "50%",
            right: "-0.08rem",
            transform: "translate(0,-50%)",
        },

        left: {
            top: "50%",
            left: "-0.08rem",
            transform: "translate(0,-50%) rotate(180deg)",
        },
    };
    return styles[props.arrowDirection];
});
</script>

<style scoped lang="scss">
.flow {
    width: 100px;
    height: 56px;
    padding: 12px;
    position: relative;

    cursor: pointer;
    &:hover {
        filter: brightness(.8);
    }
    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100%;

        background-size: 100% 100%;
    }

    .arrow {
        position: absolute;
        width: 16px;
        height: 16px;
    }
}
</style>
