<script setup>
import BMap, { BMAP_EARTH_MAP } from "BMapGL";
import { onMounted } from "vue";
import { reactive } from "vue";

const state = reactive({
    map: null,
});

const emits = defineEmits(["pointerClick"]);

onMounted(() => {
    init();
    ponits.forEach((point) => addPointOverlay(point));
});

function init() {
    const map = new BMapGL.Map("baidu-map");
    state.map = map;
    const point = new BMapGL.Point(119.98588694926113, 30.280405065311427);
    map.setMapStyleV2({ styleId: "652c32694e5100e90563c296f52bbf01" });
    map.setMapType("B_EARTH_MAP");

    map.centerAndZoom(point, 0);
    map.enableScrollWheelZoom(true);
    map.setHeading(90);
    map.setTilt(45);
}

function addPointOverlay(info) {
    const {
        location: [lng, lat],
    } = info;
    const point = new BMapGL.Point(lng, lat);
    const marker = new BMapGL.Marker(point);
    marker.addEventListener("click", () => {
        emits("pointerClick", info);
        console.log("info", info);
    });
    state.map.addOverlay(marker);
}

