<script setup>
import Flow from "./Flow/Flow.vue";
import UseDialog from "@/components/UseDialog.vue";
import { computed, nextTick, reactive, watch } from "vue";
import Join from "./FlowDetails/Join.vue";
import Artworks from "./FlowDetails/Artworks.vue";
import Service from "./FlowDetails/Service.vue";
import Push from "./FlowDetails/Push.vue";
import Done from "./FlowDetails/Done.vue";
import Buyers from "./FlowDetails/Buyers.vue";
import Apply from "./FlowDetails/Apply.vue";

const state = reactive({
    dialog: false,
    flowCode: null,
});

const FLOW_USEDIALOG_TITLES = [
    "入驻",
    "作品展现",
    "选择服务",
    "提交申报",
    "成交",
    "联系买家",
    "申请兑现",
];

const componentNames = [Join, Artworks, Service, Push, Done, Buyers, Apply];
const dialogTitle = computed(() => FLOW_USEDIALOG_TITLES[state.flowCode]);
const currentComponent = computed(() => componentNames[state.flowCode]);

function onDialogClose() {
    state.flowCode = null;
}
function onToggle(flowCode) {
    state.flowCode = flowCode;
    state.dialog = true;
}
</script>

