import { ref, onUnmounted } from "vue";
import dayjs from "dayjs";

import "dayjs/locale/zh-cn";
const PER_SECONDS = 1000;

// const Weeks = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];

export default function useImmediateTime() {
    const current = ref("");
    const date = ref("");
    const slowlyDate = ref("");

    let TIME_WATCHER = null;
    function refresh() {
        const now = dayjs();
        current.value = dayjs().format("HH:mm:ss");
        date.value =
            dayjs().format("YYYY[-]MM[-]DD");
        TIME_WATCHER = setTimeout(refresh, PER_SECONDS);
    }

    refresh();
    onUnmounted(() => {
        TIME_WATCHER && clearTimeout(TIME_WATCHER);
    });

    return { current, date };
}
