import { nextTick, onMounted, onUnmounted } from 'vue';
import * as echarts from 'echarts';
import { useEventListener } from '@vueuse/core';

export default function useChartsResize(dom) {
    return function (fn) {
        onMounted(() => {
            const element = document.querySelector(dom);
            if (!element) throw new Error('dom 不存在');
            fn && fn();
            useEventListener(window, 'resize', () => {
                nextTick(() => {
                    echarts.dispose(element);
                    fn && fn();
                });
            });
        });

        onUnmounted(() => {
            fn && window.removeEventListener('resize', fn);
        });
    };
}
