function initEcharts() {
    const element = document.querySelector(`.artworks-chart`);
    const myChart = echarts.init(element);
    myChart.setOption(getOptions());
}

function getOptions() {
    const seriesData = [
        { value: 130, name: "9分以上" },
        { value: 670, name: "8-8.9分" },
        { value: 430, name: "6-7.9分" },
        { value: 400, name: "6分以下" },
    ];

    return {
        color: ["#AA54FF", "#39EFA5", "#04E0F9", "#0099FF"],
        tooltip: {
            trigger: "item",
        },
        legend: {
            top: "center",
            right: "10%",
            width: "100px",
            icon: "circle",
            itemStyle: {
                borderWidth: 0,
            },
            textStyle: {
                color: "#fff",
                rich: {
                    name: {
                        width: fontSize(0.7),
                        padding: [0, 5],
                    },
                    percent: {
                        align: "center",
                        width: fontSize(0.5),
                    },
                    value: {
                        color: "#1DD9FF",
                    },
                },
            },
            formatter(name) {
                const total = seriesData
                    .map((item) => item.value)
                    .reduce((pre, next) => pre + next);
                const current = seriesData.find((item) => item.name === name);
                const percent = (current.value / total).toFixed(1) * 100 + "%";
                return `{name|${name}} {percent|${percent}} {value|${current.value}部}`;
            },
        },

        series: [
            {
                name: "媒体类别分析",
                type: "pie",
                radius: ["50%", "70%"],
                center: ["30%", "50%"],
                avoidLabelOverlap: false,
                type: "pie",
                itemStyle: {
                    borderWidth: 6,
                    borderColor: "#010A1B",
                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {
                    label: {
                        show: true,
                        fontSize: fontSize(0.16),
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: seriesData,
            },
        ],
    };
}
const pagination = ref({
    currentPage: 1,
    pageSize: 5,
    total: 10,
});
// 总共页数
const tableTotal = computed(() => {
    const { currentPage, total, pageSize } = pagination.value;
    let count = Math.round(total / pageSize);
    return count;
});

